import { NEWS_API_KEY } from "./news";

export const getBitcoinArticles = async () => {
  const response = await fetch(
    `https://thingproxy.freeboard.io/fetch/https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=51331d9960ec4d5eab165fda8e805372`
  );
  const json = await response.json();
  return json;
};
export const getArticles = async (topic) => {
  const response = await fetch(
    `https://newsapi.org/v2/everything?q=${topic}&sortBy=publishedAt&apiKey=${NEWS_API_KEY}`
  );
  const json = await response.json();
  return json;
};